import React from 'react'

export const PlayerSkillsNew = React.memo(({playerInfo}) => {

	const gk = React.useMemo(() => [
        { name: ' ', id: 'thr' },
		{ name: '', id: 'com' },
		{ name: '',  id: 'kic' },
		{ name: '  ', id: 'cmd' },
		{ name: ' ',  id: 'tro' },
		{ name: '  ', id: 'han' },
		{ name: ' ',      id: 'ovo' },
		{ name: ' ', id: 'pun' },
		{ name: '',   id: 'pasgk' },
		{ name: ' ',      id: 'firgk' },
		{ name: '', id: 'ref' },
		{ name: '  ',   id: 'aer' },
		{ name: '',   id: 'ecc' }
    ], [])

	const technique = React.useMemo(() => [
        { name: ' - ', id: 'lth' },
		{ name: ' ', id: 'lon' },
		{ name: '',  id: 'dri' },
		{ name: ' ', id: 'fin' },
		{ name: ' ',  id: 'hea' },
		{ name: '', id: 'fre' },
		{ name: '',      id: 'cro' },
		{ name: '', id: 'mar' },
		{ name: '',   id: 'tck' },
		{ name: '',      id: 'pas' },
		{ name: '', id: 'pen' },
		{ name: ' ',   id: 'fir' },
		{ name: '',   id: 'tec' },
		{ name: '',   id: 'cor' }
    ], [])

	const mental = React.useMemo(() => [
        { name: '', id: 'agg' },
		{ name: ' ', id: 'vis' },
		{ name: ' ',  id: 'pos' },
		{ name: '  ', id: 'otb' },
		{ name: '',  id: 'fla' },
		{ name: '', id: 'ant' },
		{ name: ' ',      id: 'tea' },
		{ name: '', id: 'cnt' },
		{ name: '',   id: 'ldr' },
		{ name: ' ',      id: 'dec' },
		{ name: '', id: 'wor' },
		{ name: '',   id: 'det' },
		{ name: '',   id: 'cmp' },
		{ name: '',   id: 'bra' }
    ], [])

	const physics = React.useMemo(() => [
        { name: '', id: 'sta' },
		{ name: ' ',      id: 'jum' },
		{ name: '', id: 'bal' },
		{ name: '',   id: 'agi' },
		{ name: ' ',      id: 'nat' },
		{ name: '', id: 'str' },
		{ name: '',   id: 'pac' },
		{ name: '',   id: 'acc' }
    ], [])

	const hidden = React.useMemo(() => [
        { name: '', id: 'amb' },
		{ name: ' ',      id: 'impm' },
		{ name: '', id: 'prof' },
		{ name: '',   id: 'injpr' },
		{ name: '',      id: 'cons' }
    ], [])

	return (
		<table className="skills-table">
			<thead>
				<tr>
					<th className="col s4 center-align"></th>
					<th className="col s4 center-align"></th>
					<th className="col s4 center-align"></th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<td className="col s4">
						<table className="striped">
							<tbody>
								{playerInfo.position === 'GK' ? 
									gk.map((attr,i) =>(
										<tr key={i}>
											<td className="table-skill-name">{attr.name}</td>
											<td 
												className="table-skill-value"
												style={{ color: 
													playerInfo[attr.id] > 15 ? 'var(--excel)' : 
													playerInfo[attr.id] > 10 ? 'var(--good)' :
													playerInfo[attr.id] > 5 ? 'var(--badly)' : 'var(--bad)'
												}}
											>
												{playerInfo[attr.id]}
											</td>
										</tr>
									))
								:
									technique.map((attr,i) =>(
										<tr key={i}>
											<td className="table-skill-name">{attr.name}</td>
											<td 
												className="table-skill-value"
												style={{ color: 
													playerInfo[attr.id] > 15 ? 'var(--excel)' : 
													playerInfo[attr.id] > 10 ? 'var(--good)' :
													playerInfo[attr.id] > 5 ? 'var(--badly)' : 'var(--bad)'
												}}
											>
												{playerInfo[attr.id]}
											</td>
										</tr>
									))
								}
							</tbody>
						</table>
					</td>
					<td className="col s4">
						<table className="striped">
							<tbody>
								{mental.map((attr,i) =>(
									<tr key={i}>
										<td className="table-skill-name">{attr.name}</td>
										<td 
											className="table-skill-value"
											style={{ color: 
												playerInfo[attr.id] > 15 ? 'var(--excel)' : 
												playerInfo[attr.id] > 10 ? 'var(--good)' :
												playerInfo[attr.id] > 5 ? 'var(--badly)' : 'var(--bad)'
											}}
										>
											{playerInfo[attr.id]}
										</td>
									</tr>
								))}
							</tbody>
						</table>
					</td>
					<td className="col s4">
						<table className="striped">
							<tbody>
								{physics.map((attr,i) =>(
									<tr key={i}>
										<td className="table-skill-name ">{attr.name}</td>
										<td 
											className="table-skill-value"
											style={{ color: 
												playerInfo[attr.id] > 15 ? 'var(--excel)' : 
												playerInfo[attr.id] > 10 ? 'var(--good)' :
												playerInfo[attr.id] > 5 ? 'var(--badly)' : 'var(--bad)'
											}}
										>
											{playerInfo[attr.id]}
										</td>
									</tr>
								))}
								<tr>
									<td className="center-align" style={{fontWeight: 'bold'}}></td>
									<td></td>
								</tr>
								{hidden.map((attr,i) =>(
									<tr key={i}>
										<td className="table-skill-name">{attr.name}</td>
										<td 
											className="table-skill-value"
											style={{ color: 
												playerInfo[attr.id] > 15 ? 'var(--excel)' : 
												playerInfo[attr.id] > 10 ? 'var(--good)' :
												playerInfo[attr.id] > 5 ? 'var(--badly)' : 'var(--bad)'
											}}
										>
											{playerInfo[attr.id]}
										</td>
									</tr>
								))}
							</tbody>
						</table>
					</td>
				</tr>
			</tbody>
		</table>
		)
})
